/* Stying for range sliders
 * mainly from https://css-tricks.com/styling-cross-browser-compatible-range-inputs-css
 */
input[type='range'] {
  -webkit-appearance: none; /* Hides the slider so that custom slider can be made */
  width: 100%; /* Specific width is required for Firefox. */
  background: transparent; /* Otherwise white in Chrome */
}

input[type='range']::-webkit-slider-thumb {
  -webkit-appearance: none;
}

input[type='range']:focus {
  outline: none; /* Removes the blue border. You should probably do some kind of focus styling for accessibility reasons though. */
}

input[type='range']::-ms-track {
  width: 100%;
  cursor: pointer;

  /* Hides the slider so custom styles can be added */
  background: transparent;
  border-color: transparent;
  color: transparent;
}

/* Range slider control styles */
/* Special styling for WebKit/Blink */
input[type='range']::-webkit-slider-thumb {
  -webkit-appearance: none;
  border: 1px solid #222;
  height: 32px;
  width: 32px;
  border-radius: 3px;
  background: #eee;
  cursor: pointer;
  /* TODO position is off in Edge */
  margin-top: -12px; /* You need to specify a margin in Chrome, but in Firefox and IE it is automatic */
}

/* All the same stuff for Firefox */
input[type='range']::-moz-range-thumb {
  border: 1px solid #222;
  height: 32px;
  width: 32px;
  border-radius: 3px;
  background: #eee;
  cursor: pointer;
}

/* All the same stuff for IE */
input[type='range']::-ms-thumb {
  border: 1px solid #222;
  height: 32px;
  width: 32px;
  border-radius: 3px;
  background: #eee;
  cursor: pointer;
}

/* Range slider track styles */
input[type='range']::-webkit-slider-runnable-track {
  width: 100%;
  height: 8.4px;
  cursor: pointer;
  background: #602d2a;
  border-radius: 1.3px;
}

input[type='range']:focus::-webkit-slider-runnable-track {
  background: #602d2a;
}

input[type='range']::-moz-range-track {
  width: 100%;
  height: 8.4px;
  cursor: pointer;
  background: #602d2a;
  border-radius: 1.3px;
}

input[type='range']::-ms-track {
  width: 100%;
  height: 8.4px;
  cursor: pointer;
  background: transparent;
  border-color: transparent;
  border-width: 16px 0;
  color: transparent;
}
input[type='range']::-ms-fill-lower {
  background: #602d2a;
  border-radius: 2.6px;
}
input[type='range']:focus::-ms-fill-lower {
  background: #602d2a;
}
input[type='range']::-ms-fill-upper {
  background: #602d2a;
  border-radius: 2.6px;
}
input[type='range']:focus::-ms-fill-upper {
  background: #602d2a;
}

.play-button {
