.circles-spinner {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
  
  .circles-spinner > div {
    background-color: #999;
    width: 20%;
    height: 20%;
  
    border-radius: 100%;
    display: inline-block;
    -webkit-animation: sk-bouncedelay 1.4s infinite ease-in-out both;
    animation: sk-bouncedelay 1.4s infinite ease-in-out both;
  }
  
  .circles-spinner .bounce1 {
    -webkit-animation-delay: -0.32s;
    animation-delay: -0.32s;
  }
  
  .circles-spinner .bounce2 {
    -webkit-animation-delay: -0.16s;
    animation-delay: -0.16s;
  }
  
  @-webkit-keyframes sk-bouncedelay {
    0%, 80%, 100% { -webkit-transform: scale(0) }
    40% { -webkit-transform: scale(1.0) }
  }
  
  @keyframes sk-bouncedelay {
    0%, 80%, 100% { 
      -webkit-transform: scale(0);
      transform: scale(0);
    } 40% { 
      -webkit-transform: scale(1.0);
      transform: scale(1.0);
    }
  }
.pad {
  position: relative;
  width: 64px;
  height: 64px;
}

.pad--cursor {
  background-color: #fcc;
}

.drum-grid {
  display: grid;
  grid-template-columns: auto auto auto auto;
  padding: 8px;
  grid-gap: 0;
  justify-items: start;
  align-items: start;
  justify-content: center;
  align-content: start;
}

.drum-grid__title {
  grid-column: 1 / -1;
  width: 100%;
  padding: 4px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  color: #999;
  font-size: 13px;
}

.drum-grid__remove {
  appearance: none;
  -moz-appearance: none;
  -webkit-appearance: none;
  border: none;
  background-color: transparent;
  color: #999;
  cursor: pointer;
}

.drum-grid__remove:hover {
  color: #eee;
}

.drum-grid__spinner-container {
  width: 236px;
  height: 236px;
  display: flex;
  justify-content: center;
  align-items: center; 
}

.pad__cover {
  width: 64px;
  height: 64px;
  background-color: #27295f;
  position: absolute;
  top: 0;
  border: 2px solid #222;
  border-radius: 10%;
  cursor: pointer;
}

.pad__cover--cursor {
  background-color: #112257;
}

.pad__checkbox {
  opacity: 0;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
}

.pad__label {
  width: 64px;
  height: 64px;
  position: absolute;
  top: 0;
  left: 0;
}

input[type='checkbox']:checked ~ div {
  background: #c43e17;
}

input[type='checkbox']:checked ~ div.pad__cover--cursor {
  opacity: 0.3;
}

